<template>
  <div
    class="tables"
    style="display: flex; align-items: start; justify-content: center"
  >
    <v-card
      flat
      tile
      width="95%"
      class="my-4 px-8 py-4 text-center"
      style="border-radius: 0 0 25px 25px !important"
    >
      <template>
        <v-tabs
          v-model="tab"
          class="d-flex justify-start mb-6 mr-4 px-4"
          align-with-title
        >
          <v-tab v-for="table in tables" :key="table" class="mx-4">
            <h3>{{ table }}</h3>
          </v-tab>
          <v-tabs-slider color="secondary"></v-tabs-slider>
        </v-tabs>
      </template>

      <v-tabs-items v-model="tab">
        <v-tab-item v-for="table in tables" :key="table">
          <div class="mt-8" v-show="table == 'PUSH VOUCHER'">
            <PushVoucher />
          </div>
          <!-- <div class="mt-8" v-show="table == 'WHITELIST'">
            <ItemWhitelist />
          </div> -->
        </v-tab-item>
      </v-tabs-items>
    </v-card>
  </div>
</template>

<script>
/* eslint-disable */
import PushVoucher from "./PushVoucher";
// import ItemWhitelist from "./ItemWhitelist";

export default {
  name: "Tables",

  components: { PushVoucher },

  data() {
    return {
      tab: null,
      // tables: ["PUSH VOUCHER", "WHITELIST"],
      tables: ["PUSH VOUCHER"],
    };
  },

  mounted() {
    // localStorage.removeItem("user");
    if (!localStorage.user_fname) {
      this.$router.push({ name: "login" }).catch((error) => {
        console.log(error);
      });
      localStorage.removeItem("user_fname");
      localStorage.removeItem("user_lname");
      localStorage.removeItem("token");
      localStorage.removeItem("nik");
    }
    var dateNow = Date.parse(new Date());
    var dateEnd = Date.parse(localStorage.date_end);
    // console.log(dateNow);
    // console.log(dateEnd);
    // console.log(dateEnd, dateNow, dateEnd < dateNow);
    if (dateEnd < dateNow) {
      this.$router.push({ name: "login" }).catch((error) => {
        console.log(error);
      });
      localStorage.removeItem("user_fname");
      localStorage.removeItem("user_lname");
      localStorage.removeItem("token");
      localStorage.removeItem("nik");
    }
  },

  created() {
    this.user = this.$store.state.u;
  },

  methods: {},
};
</script>
