<template>
  <div>
    <div>
      <v-dialog v-model="loadingDialog" persistent width="300">
        <v-card color="primary" dark>
          <v-card-text class="pa-2 pl-4 pr-4 pb-4">
            Loading, please wait
            <v-progress-linear
              indeterminate
              color="white"
              class="mb-0"
            ></v-progress-linear>
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>

    <v-dialog v-model="dialog.confirmDelete" max-width="290">
      <v-card>
        <v-card-title class="text-h5"> Hapus </v-card-title>

        <v-card-text> Apakah Anda yakin ingin menghapus item? </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="grey darken-1"
            text
            @click="dialog.confirmDelete = false"
          >
            TIDAK
          </v-btn>

          <v-btn
            color="red darken-1"
            text
            @click="
              () => {
                dialog.confirmDelete = false;
                onDelete(tempDelete);
              }
            "
          >
            YA
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-row class="ma-4 px-8" style="display: flex">
      <div
        class="ml-auto"
        style="display: flex; justify-content: center; align-items: center"
      >
        <h6 class="mt-1" style="font-weight: 300; font-size: 12px">
          Last Updated: {{ lastUpdate }}
        </h6>
        <span class="pl-6 bar-icon">
          <v-btn
            depressed
            x-small
            color="white"
            style="color: blue"
            @click="refreshTable()"
          >
            <v-icon color="blue">mdi-refresh</v-icon>Refresh
          </v-btn>
        </span>
      </div>
    </v-row>

    <v-divider></v-divider>

    <!-- Campain Table Start -->
    <div class="my-4 pa-4 px-6 d-flex flex-row" style="width: 100% !important">
      <v-card elevation="3" outlined style="width: 100% !important">
        <div class="my-4 px-6 d-flex flex-row-reverse">
          <v-col cols="2" class="ma-0 pa-0">
            <div
              class="pa-2"
              style="
                display: flex;
                justify-content: center;
                width: 100% !important;
              "
              @click="route('input-form')"
            >
              <v-btn
                height="35px"
                width="100%"
                block
                color="success"
                style="font-size: 12px; width: 100% !important"
              >
                <v-icon color="white" class="mr-2">mdi-plus</v-icon>Add
                Campaign</v-btn
              >
            </div>
          </v-col>

          <!-- <v-col class="ma-0 pa-0">
            <div
              class="py-2 px-8"
              style="display: flex; justify-content: center"
              @click="route('upload-voucher-code')"
            >
              <v-btn
                class="white--text"
                height="35px"
                block
                color="orange"
                style="font-size: 12px"
              >
                <v-icon small>mdi-upload</v-icon>&nbsp;Upload Voucher
                Code</v-btn
              >
            </div>
          </v-col> -->

          <v-col cols="9"> </v-col>
        </div>

        <v-divider></v-divider>

        <v-toolbar flat class="px-3 mt-2">
          <v-toolbar-title>Campaign</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <v-spacer></v-spacer>
          <v-text-field
            class="px-4"
            v-bind:value="search"
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
            style="width: 1%"
          ></v-text-field>
        </v-toolbar>

        <v-card flat class="my-4">
          <v-card-text class="pt-1 pb-1 px-6">
            <v-data-table :headers="headers" :items="data" :search="search">
              <template #[`item.amtv_start_date`]="{ item }">
                <div class="my-2">
                  <v-chip color="blue" dark>
                    {{ item.amtv_start_date + " s.d " + item.amtv_end_date }}
                  </v-chip>
                </div>
              </template>
              <template #[`item.amtv_qty_reward`]="{ item }">
                {{ getQty(item.amtv_qty_reward, item.amtv_qty_used) }}
              </template>
              <template #[`item.amtv_qty_used`]="{ item }">
                <v-chip
                  :color="
                    getQtyRemainingColor(
                      item.amtv_qty_reward,
                      item.amtv_qty_used
                    )
                  "
                  dark
                >
                  {{
                    getQtyRemaining(item.amtv_qty_reward, item.amtv_qty_used)
                  }}
                </v-chip>
              </template>
              <template #[`item.status_program`]="{ item }">
                <v-chip
                  :color="
                    checkDateActiveColor(
                      item.amtv_start_date,
                      item.amtv_end_date
                    )
                  "
                  dark
                >
                  {{
                    checkDateActive(item.amtv_start_date, item.amtv_end_date)
                  }}
                </v-chip>
              </template>
              <template #[`item.amtv_flag_inject`]="{ item }">
                <v-checkbox
                  v-model="item.amtv_flag_inject"
                  color="primary lighten-1"
                  readonly
                ></v-checkbox>
              </template>
              <template #[`item.amtv_date_upload_member`]="{ item }">
                <div class="my-2">
                  <v-chip color="blue" dark>
                    {{ item.amtv_date_upload_member }}
                  </v-chip>
                </div>
              </template>
              <template #[`item.actions`]="{ item }">
                <v-icon
                  :disabled="
                    !checkDateActiveBoolean(
                      item.amtv_start_date,
                      item.amtv_end_date
                    )
                  "
                  small
                  color="green"
                  @click="onEdit(item)"
                  class="mr-1"
                >
                  mdi-pencil-outline
                </v-icon>
                <v-icon
                  :disabled="
                    !checkDateActiveBoolean(
                      item.amtv_start_date,
                      item.amtv_end_date
                    )
                  "
                  small
                  color="error"
                  @click="onDeleteConfirm(item)"
                >
                  mdi-trash-can-outline
                </v-icon>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-card>
    </div>
    <!-- Campain Table End -->
  </div>
</template>

<script>
/* eslint-disable */
// import ItemWhitelist from "./ItemWhitelist";
// import VoucherCode from "./VoucherCode";

import { api_url } from "../global_variable/global_variable.js";
import axios from "axios";
import sha256 from "js-sha256";

export default {
  name: "PushVoucher",

  // components: { ItemWhitelist, VoucherCode },

  data() {
    return {
      loadingDialog: false,
      dateNow: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      lastUpdate: new Date().toLocaleString(),
      search: "",
      textInputRules: [(v) => !!v || "Kolom harus diisi"],
      dialog: {
        input: false,
        input2: false,
        confirmDelete: false,
      },
      headers: [
        {
          text: "ID",
          align: "start",
          value: "amtv_id",
          class: "style-header",
        },
        {
          text: "Title",
          value: "amtv_title",
          class: "style-header",
        },
        {
          text: "Type",
          value: "amtv_tipe_vcr",
          class: "style-header",
        },
        {
          text: "Promo Period",
          value: "amtv_start_date",
          class: "style-header",
        },
        {
          text: "Reward",
          value: "amtv_bo_reward",
          class: "style-header",
        },
        {
          text: "Voucher Qty",
          value: "amtv_qty_reward",
          class: "style-header",
        },
        {
          text: "Qty Remaining",
          value: "amtv_qty_used",
          class: "style-header",
        },
        {
          text: "Status Program",
          value: "status_program",
          class: "style-header",
        },
        {
          text: "Status Inject",
          value: "amtv_flag_inject",
          class: "style-header",
        },
        {
          text: "Inject Date",
          value: "amtv_date_upload_member",
          class: "style-header",
        },
        {
          text: "Actions",
          value: "actions",
          class: "style-header",
          sortable: false,
        },
      ],
      data: [],
      tempDelete: null,
    };
  },

  mounted() {
    this.getVoucherData();
  },

  methods: {
    async getVoucherData() {
      // console.log(api_url);
      this.loadingDialog = true;

      // GET VOUCHER EVENT START
      try {
        var d = new Date();
        var m = d.getMonth() + 1;
        var t = d.getDate();
        if (m < 10) {
          m = "0" + m;
        }
        if (t < 10) {
          t = "0" + t;
        }
        var datestring = d.getFullYear() + "" + m + "" + t;
        // console.log(datestring);
        var tgl = datestring;
        var kunci = "SSO" + "4lfag1ft" + tgl + "targeted_voucher";
        // console.log(kunci);
        var token = sha256(kunci);
        // console.log(token);

        const headers = {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Credentials": "true",
          "Access-Control-Allow-Methods":
            "GET, PUT, POST, DELETE, OPTIONS, post, get",
          "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token",
          "Content-Type": "application/json",
          "Content-Length": "<calculated when request is sent>",
          Host: "<calculated when request is sent>",
          Accept: "*/*",
          "Accept-Encoding": "gzip, deflate, br",
          Connection: "keep-alive",
          app: "sso",
        };

        await axios
          .get(api_url + "targeted-voucher?token=" + token, headers)
          .then((response) => {
            // console.log(api_url + "targeted-voucher?token=" + token);
            // console.log(response.data.data);
            this.data = response.data.data;
            if (!this.data) {
              alert(
                "Gagal mengambil data! Server timeout. Silahkan coba lagi."
              );
              return;
            }
          })
          .catch((error) => {
            console.log("PushVoucher.vue (error) :: ", error);
            alert("Gagal mengambil data! Silahkan coba lagi.");
          });

        // Break if request failed
        if (!this.data) {
          this.loadingDialog = false;
          return;
        }
      } catch (e) {
        console.error(e);
        alert("Error: koneksi gagal");
        return;
      }
      // GET VOUCHER EVENT END

      this.loadingDialog = false;
    },
    getColor(status) {
      if (status == true) return "green";
      else return "red";
    },
    checkDateActive(date_start, date_end) {
      var dateNow = Date.parse(new Date());
      var dateStart = Date.parse(date_start);
      var dateEnd = Date.parse(date_end);
      // console.log(dateEnd, dateNow, dateStart < dateNow, dateEnd < dateNow);
      if (dateStart > dateNow) {
        return "Promo belum berjalan.";
      } else if (dateEnd == dateNow) {
        return "Promo sedang berjalan.";
      } else if (dateEnd < dateNow) {
        return "Promo sudah berakhir.";
      } else {
        return "Promo sedang berjalan.";
      }
    },
    checkDateActiveColor(date_start, date_end) {
      var dateNow = Date.parse(new Date());
      var dateStart = Date.parse(date_start);
      var dateEnd = Date.parse(date_end);
      // console.log(dateEnd, dateNow, dateStart < dateNow, dateEnd < dateNow);
      if (dateStart > dateNow) {
        return "grey";
      } else if (dateEnd == dateNow) {
        return "green";
      } else if (dateEnd < dateNow) {
        return "red";
      } else {
        return "green";
      }
    },
    checkDateActiveBoolean(date_start, date_end) {
      var dateNow = Date.parse(new Date());
      var dateStart = Date.parse(date_start);
      var dateEnd = Date.parse(date_end);
      // console.log(dateEnd, dateNow, dateStart < dateNow, dateEnd < dateNow);
      if (dateStart > dateNow) {
        return true;
      } else if (dateEnd == dateNow) {
        return false;
      } else if (dateEnd < dateNow) {
        return false;
      } else {
        return false;
      }
    },
    getQty(qtyInit, qtyUsed) {
      if (qtyInit == null || qtyInit == undefined) {
        return "-";
      }
      return qtyInit;
    },
    getQtyRemainingColor(qtyInit, qtyUsed) {
      // console.log(qtyRemaining, qtyInit, qtyUsed);
      if (qtyInit == null || qtyInit == undefined) {
        return "grey";
      }

      qtyInit = parseInt(qtyInit);
      qtyUsed = parseInt(qtyUsed);

      var qtyRemaining = qtyInit - qtyUsed;
      // console.log(qtyRemaining, qtyInit, qtyUsed);

      if (qtyRemaining <= 30) {
        return "red";
      } else {
        return "green";
      }
    },
    getQtyRemaining(qtyInit, qtyUsed) {
      if (qtyInit == null || qtyInit == undefined) {
        return " - ";
      } else if (qtyUsed == "") {
        return qtyInit;
      } else if (qtyUsed == null || qtyUsed == undefined) {
        return qtyInit;
      } else {
        qtyInit = parseInt(qtyInit);
        qtyUsed = parseInt(qtyUsed);

        var qtyRemaining = qtyInit - qtyUsed;
        // console.log(qtyRemaining, qtyInit, qtyUsed);

        return qtyRemaining;
      }
    },

    route(route) {
      this.$router.push({ name: route }).catch((error) => {
        console.log(error);
      });
    },

    async onEdit(item) {
      // console.log(item, item.amtv_id);

      var dateNow = Date.parse(new Date());
      var dateStart = Date.parse(item.amtv_start_date);
      var dateEnd = Date.parse(item.amtv_end_date);
      // console.log(dateEnd, dateNow, dateStart < dateNow, dateEnd < dateNow);
      if (dateStart > dateNow) {
        // return "Promo belum berjalan.";
        this.$router
          .push({ name: "edit-form", params: { campaign_data: item } })
          .catch((error) => {
            console.log(error);
          });
      } else if (dateEnd == dateNow) {
        // return "Promo sedang berjalan.";
        alert("Campaign yang sedang berjalan tidak bisa di edit.");
      } else if (dateEnd < dateNow) {
        // return "Promo sudah berakhir.";
        alert("Campaign yang sudah berakhir tidak bisa di edit.");
      } else {
        // return "Promo sedang berjalan.";
        alert("Campaign yang sedang berjalan tidak bisa di edit.");
      }

      // alert("Edit item!");
    },
    onDeleteConfirm(item) {
      var dateNow = Date.parse(new Date());
      var dateStart = Date.parse(item.amtv_start_date);
      var dateEnd = Date.parse(item.amtv_end_date);
      // console.log(dateEnd, dateNow, dateStart < dateNow, dateEnd < dateNow);
      if (dateStart > dateNow) {
        // return "Promo belum berjalan.";
        this.tempDelete = item;
        this.dialog.confirmDelete = true;
      } else if (dateEnd == dateNow) {
        // return "Promo sedang berjalan.";
        alert("Campaign yang sedang berjalan tidak bisa di hapus.");
      } else if (dateEnd < dateNow) {
        // return "Promo sudah berakhir.";
        alert("Campaign yang sudah berakhir tidak bisa di hapus.");
      } else {
        // return "Promo sedang berjalan.";
        alert("Campaign yang sedang berjalan tidak bisa di hapus.");
      }
    },
    async onDelete(item) {
      // console.log(item.amtv_id);

      this.loadingDialog = true;

      try {
        var d = new Date();
        var m = d.getMonth() + 1;
        var t = d.getDate();
        if (m < 10) {
          m = "0" + m;
        }
        if (t < 10) {
          t = "0" + t;
        }
        var datestring = d.getFullYear() + "" + m + "" + t;
        var tgl = datestring;
        var kunci =
          "SSO" + "4lfag1ft" + tgl + "targeted_voucher" + item.amtv_id;
        var token = sha256(kunci);

        const headers = {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Credentials": "true",
          "Access-Control-Allow-Methods":
            "GET, PUT, POST, DELETE, OPTIONS, post, get",
          "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token",
          "Content-Type": "application/json",
          "Content-Length": "<calculated when request is sent>",
          Host: "<calculated when request is sent>",
          Accept: "*/*",
          "Accept-Encoding": "gzip, deflate, br",
          Connection: "keep-alive",
          app: "sso",
        };

        await axios
          .delete(
            api_url +
              "targeted-voucher?token=" +
              token +
              "&amtv_id=" +
              item.amtv_id,
            headers
          )
          .then((response) => {
            console.log(response.data);
            if (response.data.data.status) {
              this.refreshTable();
              alert("Berhasil menghapus item!");
            } else {
              alert("Gagal menghapus item! Silahkan coba lagi.");
            }

            // this.data = response.data.data;
          })
          .catch((error) => {
            console.log("PushVoucher.vue (error) :: ", error);
            alert("Gagal menghapus voucher!");
          });
      } catch (e) {
        console.error(e);
        alert("Error: koneksi gagal");
      }

      this.tempDelete = null;

      this.refreshTable();

      this.loadingDialog = false;
    },
    async refreshTable() {
      this.loadingDialog = true;

      this.lastUpdate = new Date().toLocaleString();

      this.data = [];

      // GET VOUCHER EVENT START
      try {
        var d = new Date();
        var m = d.getMonth() + 1;
        var t = d.getDate();
        if (m < 10) {
          m = "0" + m;
        }
        if (t < 10) {
          t = "0" + t;
        }
        var datestring = d.getFullYear() + "" + m + "" + t;
        var tgl = datestring;
        var kunci = "SSO" + "4lfag1ft" + tgl + "targeted_voucher";
        var token = sha256(kunci);
        // console.log(token);

        const headers = {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Credentials": "true",
          "Access-Control-Allow-Methods":
            "GET, PUT, POST, DELETE, OPTIONS, post, get",
          "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token",
          "Content-Type": "application/json",
          "Content-Length": "<calculated when request is sent>",
          Host: "<calculated when request is sent>",
          Accept: "*/*",
          "Accept-Encoding": "gzip, deflate, br",
          Connection: "keep-alive",
          app: "sso",
        };

        await axios
          .get(api_url + "targeted-voucher?token=" + token, headers)
          .then((response) => {
            // console.log(response.data.data);
            this.data = response.data.data;
            if (!this.data) {
              alert(
                "Gagal mengambil data! Server timeout. Silahkan coba lagi."
              );
              return;
            }
          })
          .catch((error) => {
            console.log("PushVoucher.vue (error) :: ", error);
            alert("Gagal mengambil data! Silahkan coba lagi.");
          });

        // Break if request failed
        if (!this.data) {
          this.loadingDialog = false;
          return;
        }
      } catch (e) {
        console.error(e);
        alert("Error: koneksi gagal");
        return;
      }
      // GET VOUCHER EVENT END

      this.loadingDialog = false;
    },
  },
};
</script>

<style>
.style-header {
  white-space: nowrap !important;
  margin: auto auto !important;
  justify-items: center !important;
  align-items: center !important;
  background-color: #f5f5f5 !important;
  color: black !important;
  text-align: center !important;
}
</style>